import React from 'react';
import Modal from 'react-modal';
import Select from 'react-select';
import './styles/AddModalStyle.css'; // Importez le fichier CSS

import exit from './images/exit-page.svg';

function AddStaffModal({ isOpen, onRequestClose }) {

    const ranksList = [
        { value: 'sdt', label: 'Sdt' },
        { value: 'cpl', label: 'Cpl' },
        { value: 'mcpl', label: 'MCpl' },
        { value: 'sgt', label: 'Sgt' },
        { value: 'wo', label: 'WO' },
        { value: 'mwo', label: 'MWO' },
        { value: 'cwo', label: 'CWO' },
        { value: 'ocdt', label: 'OCdt' },
        { value: '2lt', label: '2Lt' },
        { value: 'lt', label: 'Lt' },
        { value: 'capt', label: 'Capt' },
        { value: 'maj', label: 'Maj' },
        { value: 'lcol', label: 'LCol' },
        { value: 'col', label: 'Col' }
    ];

    const unitsList = [
        { value: '5008', label: '5008 - 12 RBC (M)' },
        { value: '3010', label: '3010 - 2 Cdn Div HQ' }
    ];

    const qualificationsList = [
        { value: 'driving-milcot', label: 'Milcot Driver' },
        { value: 'driving-gwagon', label: 'Gwagon Driver' },
        { value: 'driving-hazardmat', label: 'Hazard Material Driver' },
    ];

    const tradesList = [
        { value: '00005', label: '00005 - Armoured' },
        { value: '00178', label: '00178 - Armoured Officer' },
        { value: '00010', label: '00010 - Infantry' },
        { value: '00180', label: '00180 - Infantry Officer' },
        { value: '00099', label: '00099 - Intelligence Op' },
        { value: '00207', label: '00207 - Naval Warfare Officer' },
        { value: '00164', label: '00164 - Cook' }
    ];

    return (
        <Modal
            isOpen={isOpen}
            onRequestClose={onRequestClose}
            className="AddStaffModal"
            overlayClassName={"modal-overlay"}
        >

            
            
            <div className="modal-header">
                <h3>Nouvelle VEM</h3>
                <button onClick={onRequestClose} className="modal-exit-button"><img src={exit} alt="Exit"/></button>
                
            </div>
            <hr />

            <div className="modal-body">
                <div className="left-section">
                    <label>
                        Title:
                        <input className="modal-text-input" type="text" />
                    </label>
                    <div className="left-section-division">
                        <label>
                            Start Date:
                            <input className="modal-text-input" type="date" />
                        </label>
                        <label>
                            End Date:
                            <input className="modal-date-input" type="date" />
                        </label>
                        <label>
                            Limit Date:
                        <input className="modal-date-input" type="date" />
                    </label>
                    </div>
                    
                    <label>
                        Attchements (PDF):
                        <input className="modal-file-input" type="file" />
                    </label>
                    <label>
                        Location:
                        <input className="modal-text-input" type="text" />
                    </label>
                    <label className="modal-textarea-input">
                        Description:
                        <textarea />
                    </label>
                    <div className="left-section-division">
                        <label>
                            Ranks targeted (required):
                            <Select 
                                className="modal-select-input" 
                                options={ranksList}
                                isMulti
                            />
                        </label>
                        <label>
                            Units targeted (required):
                            <Select 
                                className="modal-select-input" 
                                options={unitsList}
                                isMulti
                            />
                        </label>
                    </div>
                    <div className="left-section-division">
                        <label>
                            Trades required (optional):
                            <Select 
                                className="modal-select-input" 
                                options={tradesList}
                                isMulti
                            />
                        </label>
                        <label>
                            Qualifications required (optional):
                            <Select 
                                className="modal-select-input" 
                                options={qualificationsList}
                                isMulti
                            />
                        </label>
                    </div>
                    
                    
                </div>
                <div className="right-section">
                    
                </div>
            </div>
            <div className="modal-footer">
                <button className="send-button" onClick={onRequestClose}>Envoyer la VEM</button>
            </div>
        </Modal>
    );
}

export default AddStaffModal;